<template>
  <div>
    <img class="background-img" src="../assets/background.png"/>
    <div class="mainContainer">
      <header>
        <div class="wrap-left">
          <img class="logo" src="../assets/logo.png"/>
          <span class="title">规范汉字书写智慧课堂</span>
        </div>
        <div class="wrap-right">
          <button class="info">客服咨询</button>
        </div>
      </header>
      <div class="signin-wrap">
        <div class="signin">
          <div class="signin-content-wrap">
            <span class="signin-title" ref="signinTitle">账号登录</span><br>
            <input v-model="phone" class="phone" type="text" ref="signinPhone" placeholder="手机号码"/>
            <button class="signin-button" ref="signinButton" @click="login()">登录</button>
          </div>
        </div>
      </div>
      <span class="version">书写中国V-2.0.1</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'signin',
  data () {
    return {
      phone: ''
    }
  },
  methods: {
    login: function () {
      console.log(this.phone)
      if (this.phone === '1145141919810') {
        window.location.href = '/#/video'
      }
    }
  },
  created () {
    this.$nextTick(() => {
      let phoneElement = this.$refs.signinPhone
      let phoneWidth = parseInt(window.getComputedStyle(phoneElement).width)
      let phoneElementMargin = -(phoneWidth / 2)
      console.log(phoneElementMargin)
      phoneElement.style.marginLeft = phoneElementMargin.toString() + 'px'
      let buttonElement = this.$refs.signinButton
      let buttonWidth = parseInt(window.getComputedStyle(buttonElement).width) + 20
      let buttonElementMargin = -(buttonWidth / 2)
      buttonElement.style.marginLeft = buttonElementMargin.toString() + 'px'
      buttonElement.style.width = buttonWidth.toString() + 'px'
      let titleElement = this.$refs.signinTitle
      let titleWidth = parseInt(window.getComputedStyle(titleElement).width)
      let titleElementMargin = -(titleWidth / 2)
      titleElement.style.marginLeft = titleElementMargin.toString() + 'px'
    })
  }
}
</script>

<style scoped>
  .mainContainer{
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    overflow: hidden;
  }
  .background-img{
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
  }
  header{
    height: 11%;
    width: 100%;
    background: white;
    position: relative;
  }
  .logo{
    height: 65%;
    margin-left: 100px;
  }
  .wrap-left{
    height: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-items: center;
  }
  .wrap-right{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-items: center;
  }
  .title{
    font-family: "Microsoft YaHei UI Light";
    margin-left: 65px;
    border-left: 1px solid black;
    padding-left: 50px;
    height: 60%;
    font-size: 2.7em;
    font-weight: 400;
    padding-top: 10px;
  }
  .info{
    margin-left: auto;
    margin-right: 150px;
    width: 150px;
    border: none;
    background: linear-gradient(270deg,#4193f0,#67abff);
    height: 60%;
    border-radius: 29px;
    font-size: 2em;
    font-family: "Microsoft YaHei UI";
    color: white;
  }
  .signin{
    height: 350px;
    width: 500px;
    background: white;
    margin-bottom: 11%;
    margin-left: 17%;
    border-radius: 30px;
    box-shadow: 0 0 30px 2px rgba(16,16,16,.11);
  }
  .signin-wrap{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-items: center;
  }
  .signin-title {
    font-size: 2.5em;
    font-family: "Microsoft YaHei UI";
    float: none;
    position: absolute;
    left: 50%;
    margin-top: 30px;
  }
  .signin-content-wrap{
    height: 100%;
    width: 100%;
    position: relative;
  }
  .phone{
    position: absolute;
    left: 50%;
    width: 80%;
    margin-left: -50%;
    width: 80%;
    margin-top: 75px;
    height: 50px;
    border-radius: 29px;
    background: #efefef;
    border: none;
    padding-left: 20px;
  }
  .signin-button{
    position: absolute;
    left: 50%;
    width: 80%;
    margin-left: -50%;
    width: 80%;
    margin-top: 200px;
    height: 50px;
    border-radius: 29px;
    background: linear-gradient(270deg,#4193f0,#67abff);
    border: none;
    color: white;
    font-family: "Microsoft YaHei UI";
    font-size: 1.5em;
  }
  input,button{
    outline: none;
  }
  .version{
    position: fixed;
    right: 5px;
    bottom: 5px;
  }
</style>



